@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bebas+Neue&family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bebas+Neue&family=Montserrat:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bebas+Neue&family=Montserrat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* Container */
.container {
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.img-container {
    /* width: 100vw;
	height: 500px; */
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.text-container {
    margin-bottom: 2vh;
}
.side-padding {
    padding-left: 6vw;
    padding-right: 6vw;
}

/* Text */
.title {
    font-family: 'Audiowide';
    font-style: normal;
    font-weight: 400;
    font-size: 4.8em;
    line-height: 115%;
    display: flex;
    align-items: center;
    letter-spacing: 0.005em;
    text-transform: capitalize;  
}
.heading {
    font-family: 'Audiowide';
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 115%;
    display: flex;
    align-items: center;
    letter-spacing: 0.005em;
    text-transform: capitalize;
}
.heading-padding {
    padding-top: 25vh;
}
.text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1.4em;
    line-height: 102.8%;
}

/* Buttons */
.btn {
    width: 222px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    line-height: 16.9px;
    letter-spacing: 0.5%;
    padding: 12px;
    text-align: center;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-self: center;
}
.btn-style-orange {
    color: white;
    background-color: #F0A4504D;
    border: 1px solid #F0A450;
}
.btn-style-orange-solid {
    color: white;
    background-color: #f0a550;
    border: 1px solid #F0A450;
}
.btn-style-blue-light {
    color: white;
    background-color: #4c6e9d4d;
    border: 1px solid #4C6E9D;
}
.btn-style-blue-solid {
    color: white;
    background-color: #4C6E9D;
    border: 1px solid #4C6E9D;
}
/* Link */
.link {
    color: white;
    text-decoration: none;
}
/* Background */
.bg-hero {
    height: 100vh;
    width: 100vw;
    background-image: url(/live-life-dao-ui/static/media/bg-hero.5b55e4b8.png);
    background-position: 65%;
    background-repeat: no-repeat;
}
.bg-rocket {
    background-image: url(/live-life-dao-ui/static/media/footer-bg.5626429c.jpg);
    background-size: cover;
    background-position: 67% 350px;
    background-repeat: no-repeat;
}
video {
    object-fit: cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;    
}
.video-wrapper {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* Animations */
.zoom {
    transition: -webkit-transform .9s;
    transition: transform .9s;
    transition: transform .9s, -webkit-transform .9s;
}
.zoom:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

/* Responsiveness */
@media screen and (min-width: 1000px) {
    .bg-hero {
        background-position: center;
        background-size: cover;
    }
    .title {
        font-weight: 400;
        font-size: 72px;
        line-height: 37px;
    }
    .subheading {
        font-weight: 600;
        font-size: 30px;
        line-height: 12px;
    }
    .text {
        font-size: 18px;
        line-height: 18.5px;
    }
    .btn {
        width: 260px;
        height: 43px;
        font-size: 18px;
        text-align: center;
    }
    .container {
        margin: 0;
        padding: 0;
        width: 60vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-self: center;
        align-items: center;
        line-height: 18.5px;
    }
    .text {
        display: inline;
    }
    .bg-rocket {
        background-position: 40% 450px;
    }
    .heading-margin {
        margin-top: 20vh;
    }
}

.nav-logo {
    width: 18vw;
}
.nav-icon {
    padding-right: 3vw;
    height: 3vh;
}
.nav-btn {
    font-family: 'Montserrat';
    font-style: normal;
    cursor: pointer;
    font-size: 1em;
    text-align: center;
    text-decoration: none;
